.gardening-services {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(135deg, #2d5a27 0%, #4a7c59 25%, #6b8e23 50%, #8fbc8f 75%, #98fb98 100%);
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

.services-header {
  text-align: center;
  margin-bottom: 50px;
  color: #fff;
  max-width: 800px;
}

.services-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 15px;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
}

.services-header p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin: 0;
  line-height: 1.5;
}

.services-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  max-width: 1200px;
  width: 100%;
}

.service-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.service-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(76, 175, 80, 0.3);
}

.service-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  text-align: center;
}

.service-card h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2d5a27;
  margin-bottom: 15px;
  text-align: center;
}

.service-description {
  color: #555;
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 0.95rem;
}

.service-features {
  list-style: none;
  padding: 0;
  margin-bottom: 25px;
}

.service-features li {
  color: #2d5a27;
  margin-bottom: 8px;
  font-size: 0.9rem;
  font-weight: 500;
}

.service-button {
  width: 100%;
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  border: none;
  padding: 15px 20px;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(76, 175, 80, 0.3);
}

.service-button:hover {
  background: linear-gradient(45deg, #45a049, #4CAF50);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(76, 175, 80, 0.4);
}

.service-button:active {
  transform: translateY(0);
}

/* 팝업 스타일 */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.popup-content {
  background: white;
  border-radius: 20px;
  padding: 30px;
  max-width: 400px;
  width: 90%;
  text-align: center;
  position: relative;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: popupSlideIn 0.3s ease-out;
}

@keyframes popupSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.popup-close {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 2rem;
  color: #999;
  cursor: pointer;
  transition: color 0.3s ease;
}

.popup-close:hover {
  color: #333;
}

.popup-image {
  margin-bottom: 20px;
}

.popup-image img {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.popup-text h3 {
  color: #2d5a27;
  font-size: 1.3rem;
  margin-bottom: 10px;
  font-weight: 600;
}

.popup-text p {
  color: #666;
  margin-bottom: 8px;
  line-height: 1.5;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .services-header h1 {
    font-size: 2rem;
  }
  
  .services-header p {
    font-size: 1rem;
  }
  
  .services-container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .service-card {
    padding: 25px;
  }
  
  .service-icon {
    font-size: 2.5rem;
  }
  
  .service-card h2 {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  .gardening-services {
    padding: 20px 15px;
  }
  
  .services-header h1 {
    font-size: 1.8rem;
  }
  
  .service-card {
    padding: 20px;
  }
  
  .popup-content {
    padding: 20px;
    margin: 20px;
  }
} 